<template>
    <div id="users">
        <div
            class="modal fade"
            id="tasksModal"
            tabindex="-1"
            aria-labelledby="tasksModalLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="tasksModalLabel">Tareas del usuario</h5>
                        <button
                            type="button"
                            class="btn-close"
                            data-mdb-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12">
                                    <table class="table table-striped">
                                        <thead>
                                        <tr>
                                            <th scope="col">Nombre</th>
                                            <th scope="col">Comando</th>
                                            <th scope="col">Acciones</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr v-for="task of taskByUser" v-bind:key="task._id">
                                            <td>{{ task.name }}</td>
                                            <td>{{ task.command }}</td>
                                            <td>
                                                <div class="btn-group">
                                                    <button type="button" class="btn btn-outline-info"
                                                            data-mdb-ripple-color="dark"
                                                            @click="executeCron(task._id)">
                                                        <i class="fas fa-forward"></i>
                                                    </button>
                                                    <button type="button" class="btn btn-outline-secondary"
                                                            data-mdb-ripple-color="dark"
                                                            @click="deleteTask(task._id)">
                                                        <i class="fas fa-trash"></i>
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                        </tbody>

                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" data-mdb-dismiss="modal">
                            Cerrar
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div
            class="modal fade"
            id="exampleModal2"
            tabindex="-1"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Usuarios</h5>
                        <button
                            type="button"
                            class="btn-close"
                            data-mdb-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <form>
                            <div class="row">
                                <div class="col-md-12">
                                    <div class="mb-3">
                                        <label for="nameTask" class="col-form-label f-l">
                                            <strong>
                                                Nombre:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="nameTask" v-model="newUser.name"/>
                                    </div>
                                </div>
                                <div class="col-md-12">
                                    <div class="mb-3">
                                        <label for="commandTask" class="col-form-label f-l">
                                            <strong>
                                                Ruta:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="commandTask"
                                               v-model="newUser.path"/>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-mdb-dismiss="modal">
                            Close
                        </button>
                        <button type="button" @click="setUser" class="btn btn-primary">Send message</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="container-fluid">
            <div class="row">
                <div class="col-md-12">
                    <div class="f-l">
                        <button type="button" class="btn btn-outline-primary" data-mdb-toggle="modal"
                                data-mdb-target="#exampleModal2" data-mdb-ripple-color="dark">
                            Nuevo
                        </button>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-md-12">
                    <table class="table table-stripped">
                        <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Nombre</th>
                            <th scope="col">Path</th>
                            <th>Acciones</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="user of users" v-bind:key="user._id">
                            <th>{{ user._id }}</th>
                            <th>{{ user.name }}</th>
                            <th>{{ user.path }}</th>
                            <th>
                                <div class="btn-group" role="group" aria-label="Basic example">
                                    <button type="button" class="btn btn-outline-info" data-mdb-ripple-color="dark"
                                            @click="createCrontab(user._id)">
                                        <i class="fas fa-forward"></i>
                                    </button>
                                    <button class="btn btn-outline-primary" data-mdb-ripple-color="dark"
                                            data-mdb-toggle="modal"
                                            data-mdb-target="#tasksModal" @click="getTasksByUserId(user._id)">
                                        <i class="fas fa-tasks"></i>
                                    </button>
                                    <button type="button" class="btn btn-outline-secondary"
                                            data-mdb-ripple-color="dark"
                                            @click="deleteUser(user._id)">
                                        <i class="fas fa-trash"></i>
                                    </button>
                                </div>
                            </th>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: "Usuarios",
    data: () => {
        return {
            newUser: {
                path: '',
                name: '',
            },
            users: [],
            taskByUser: [],
        }
    },
    methods: {
        setUser: function () {
            axios.post(`http://localhost:3000/api/v1/user/`,
                {
                    user: {
                        ...this.newUser,
                    }
                }
            )
        },
        getAllUsers: async function () {
            let {data: {users}} = await axios.get(`http://localhost:3000/api/v1/user/`);
            this.users = users
        },
        executeCron: async function (id) {
            let response = await axios.get(`http://localhost:3000/api/v1/task/${id}/execute`);
            console.log(response);
        },
        createCrontab: async function (id) {
            await axios.post(`http://localhost:3000/api/v1/user/createCron/${id}`);
            this.$notify('this is a test');

        },
        deleteUser: async function (id) {
            try {
                const response = await axios.delete(`http://localhost:3000/api/v1/user/${id}`);
                console.log(response)
            } catch (e) {
                console.log(e)
            }
        },

        getTasksByUserId: async function (user) {
            try {
                const response = await axios.get(`http://localhost:3000/api/v1/task/user/${user}`);
                this.taskByUser = response.data.tasks;
            } catch (e) {
                console.log(e)
            }
        },
        deleteTask: async function (id) {
            try {
                let response = await axios.delete(`http://localhost:3000/api/v1/task/${id}`,)
                console.log(response)
                this.taskByUser = this.taskByUser.filter(el => el._id !== id);
            } catch (e) {
                console.log(e)
            }
        }
    },
    mounted() {
        this.getAllUsers();
    },
}
</script>

<style scoped>
#users .f-l {
    float: left;
}
</style>