<template>
    <div id="tasks">
        <div v-if="logs.log.length > 0">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-12">
                        <div class="card">
                            <div class="card-header">
                                <div class="row">
                                    <div class="col-md-10">
                                        <h2 class="card-title">Logs</h2>
                                    </div>
                                    <div class="col-md-1 mr-0">
                                        <i class="fas fa-times align-left" @click="logs = {log: [], logStdout: []}"></i>
                                    </div>
                                </div>
                            </div>
                            <div class="card-body">
                                <div class="row">
                                    <div class="col-md-6">
                                        <ul class="list-group">
                                            <li class="list-group-item">LOG</li>
                                            <li class="list-group-item" v-for="log of logs.log" v-bind:key="log">{{
                                                    log
                                                }}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-md-6">
                                        <ul class="list-group">
                                            <li class="list-group-item">LOG STDOUT</li>
                                            <li class="list-group-item" v-for="logStdout of logs.logStdout"
                                                v-bind:key="logStdout">{{ logStdout }}
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div
            class="modal fade"
            id="cronModal"
            tabindex="-1"
            aria-labelledby="cronModalLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="cronModalLabel">Archivos cron encontrados</h5>
                        <button
                            type="button"
                            class="btn-close"
                            data-mdb-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12">
                                    <table class="table table-striped">
                                        <thead>
                                        <tr>
                                            <th scope="col">Nombre</th>
                                            <th scope="col">Acciones</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr v-for="cron of cronImport" v-bind:key="cron">
                                            <td>{{ cron }}</td>
                                            <td>
                                                <div class="btn-group">
                                                    <button type="button" class="btn btn-outline-info"
                                                            data-mdb-ripple-color="dark"
                                                            @click="importCrontab(cron)">
                                                        <i class="fas fa-file-import"></i>
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                        </tbody>

                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12">
                                    <form action="">
                                        <select v-model="cronUserImport" class="form-select"
                                                aria-label="Selecciona usuario al que importar">
                                            <option selected>Selecciona usuario al que importar</option>
                                            <option v-for="user of users" v-bind:key="user._id" :value="user._id">{{
                                                    user.name
                                                }}
                                            </option>
                                        </select>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <button type="button" class="btn btn-primary" data-mdb-dismiss="modal">
                            Cerrar
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div
            class="modal fade"
            id="exampleModal"
            tabindex="-1"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Tarea</h5>
                        <button
                            type="button"
                            class="btn-close"
                            data-mdb-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <form>
                            <div class="row">
                                <div class="col-md-12">
                                    <div class="mb-3">
                                        <label for="nameTask" class="col-form-label f-l">
                                            <strong>
                                                Nombre:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="nameTask" v-model="newTask.name"/>
                                    </div>
                                </div>
                                <div class="col-md-12">
                                    <div class="mb-3">
                                        <label for="commandTask" class="col-form-label f-l">
                                            <strong>
                                                Comando:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="commandTask"
                                               v-model="newTask.command"/>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-2">
                                    <div class="mb-3">
                                        <label for="minuteTask" class="col-form-label f-l">
                                            <strong>
                                                Minuto:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="minuteTask"
                                               v-model="newTask.minute"/>
                                    </div>
                                </div>
                                <div class="col-md-2">
                                    <div class="mb-3">
                                        <label for="hourTask" class="col-form-label f-l">
                                            <strong>
                                                Hora:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="hourTask" v-model="newTask.hour"/>
                                    </div>
                                </div>
                                <div class="col-md-2">
                                    <div class="mb-3">
                                        <label for="dayTask" class="col-form-label f-l">
                                            <strong>
                                                Día:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="dayTask" v-model="newTask.day"/>
                                    </div>
                                </div>
                                <div class="col-md-2">
                                    <div class="mb-3">
                                        <label for="monthTask" class="col-form-label f-l">
                                            <strong>
                                                Mes:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="monthTask" v-model="newTask.month"/>
                                    </div>
                                </div>
                                <div class="col-md-2">
                                    <div class="mb-3">
                                        <label for="weekTask" class="col-form-label f-l">
                                            <strong>
                                                Semana:
                                            </strong>
                                        </label>
                                        <input type="text" class="form-control" id="weekTask" v-model="newTask.week"/>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-12">
                                    <div class="mb-3">
                                        <label for="job" class="col-form-label f-l">
                                            <strong>
                                                Trabajo:
                                            </strong>
                                        </label>
                                        <input readonly type="text" class="form-control" id="job" v-model="job"/>
                                    </div>
                                </div>
                                <div class="col-md-12">
                                    <select v-model="newTask.user" class="form-select"
                                            aria-label="Default select example">
                                        <option selected>Seleccionar usuarios</option>
                                        <option v-for="user of users" v-bind:key="user._id" :value="user._id">{{
                                                user.name
                                            }}
                                        </option>
                                    </select>
                                </div>
                            </div>

                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" id="closeModal" class="btn btn-secondary" data-mdb-dismiss="modal">
                            Close
                        </button>
                        <button type="button" @click="setTask" class="btn btn-primary">Send message</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="container-fluid">
            <div class="row">
                <div class="col-md-12">
                    <div class="f-l btn-group">
                        <button type="button" class="btn btn-outline-primary" data-mdb-toggle="modal"
                                data-mdb-target="#exampleModal" data-mdb-ripple-color="dark">
                            Nuevo
                        </button>
                        <button type="button" class="btn btn-outline-secondary" data-mdb-ripple-color="dark"
                                data-mdb-toggle="modal"
                                data-mdb-target="#cronModal"
                                @click="getCrontabs">
                            Importar crontab
                        </button>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-md-12">
                    <div class="card">
                        <div class="card-body">

                            <table class="table table-stripped">
                                <thead>
                                <tr>
                                    <th>Nombre</th>
                                    <th>Usuario</th>
                                    <th>Comando</th>
                                    <th>Horario</th>
                                    <th>Creado el:</th>
                                    <th>Acciones</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="task of tasks" v-bind:key="task._id">
                                    <!--                            <th class="t-a-l">{{ task._id }}</th>-->
                                    <th>{{ task.name }}</th>
                                    <th>{{ getUserFilter(task.user).name }}</th>
                                    <th>{{ task.command }}</th>
                                    <th>{{ task.schedule }}</th>
                                    <th>{{ formatDate(task.timestamp) }}</th>
                                    <th>
                                        <div class="btn-group" role="group" aria-label="Basic example">
                                            <button :disabled="task.stopped" type="button" class="btn btn-outline-info"
                                                    data-mdb-ripple-color="dark"
                                                    @click="executeCron(task._id)">
                                                <i class="fas fa-forward"></i>
                                            </button>
                                            <button v-if="task.stopped === false" type="button"
                                                    class="btn btn-outline-danger"
                                                    data-mdb-ripple-color="dark"
                                                    @click="switchStopped(task)">
                                                <i class="fas fa-stop"></i>
                                            </button>
                                            <button v-else type="button" class="btn btn-outline-success"
                                                    data-mdb-ripple-color="dark"
                                                    @click="switchStopped(task)">
                                                <i class="fas fa-play"></i>
                                            </button>
                                            <button type="button" class="btn btn-outline-secondary"
                                                    data-mdb-ripple-color="dark"
                                                    @click="deleteTask(task._id)">
                                                <i class="fas fa-trash"></i>
                                            </button>
                                            <button type="button" class="btn btn-outline-warning"
                                                    data-mdb-ripple-color="dark"
                                                    @click="getLogs(task._id)">
                                                <i class="fas fa-file-alt"></i>
                                            </button>
                                        </div>
                                    </th>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        name: "Tareas",
        data: () => {
            return {
                newTask: {
                    minute: '*',
                    hour: '*',
                    day: '*',
                    month: '*',
                    week: '*',
                    command: '',
                    name: '',
                    user: null,
                },
                fullJob: '',
                tasks: [],
                users: [],
                cronImport: [],
                cronUserImport: null,
                logs: {
                    log: [],
                    logStdout: [],
                },
            }
        },
        methods: {
            setTask: async function () {
                try {
                    let response = await axios.post(`http://localhost:3000/api/v1/task/`,
                        {
                            task: {
                                ...this.newTask,
                                schedule: `${this.newTask.minute} ${this.newTask.hour} ${this.newTask.day} ${this.newTask.month} ${this.newTask.week}`,
                            }
                        }
                    );
                    this.tasks.push(response.data.task);

                    document.getElementById('closeModal').click();
                    // $('#exampleModal').modal('hide');
                } catch (e) {
                    console.log(e.toString())
                }
            },
            getAllTasks: async function () {
                let {data: {tasks}} = await axios.get(`http://localhost:3000/api/v1/task/`);
                this.tasks = tasks
            },
            getAllUsers: async function () {
                let {data: {users}} = await axios.get(`http://localhost:3000/api/v1/user/`);
                this.users = users
            },
            executeCron: async function (id) {
                try {
                    let response = await axios.get(`http://localhost:3000/api/v1/task/${id}/execute`);
                    console.log(response)
                } catch (e) {
                    console.log(e)
                }
            },
            getUserFilter: function (id) {
                let filter = this.users.filter(el => el._id === id)
                return filter.length > 0 ? filter[0] : {};
            },
            formatDate: function (date) {
                let newDate = new Date(date);
                return `${newDate.getDate()}-${newDate.getMonth() + 1}-${newDate.getFullYear()}`
            },
            updateTask: async function (task) {
                try {
                    task.id = task._id
                    let response = await axios.put(`http://localhost:3000/api/v1/task/`, {
                        task: {...task}
                    });
                    console.log(response);
                } catch (e) {
                    console.log(e)
                }

            },
            deleteTask: async function (id) {
                try {
                    let response = await axios.delete(`http://localhost:3000/api/v1/task/${id}`,)
                    console.log(response)
                    this.tasks = this.tasks.filter(el => el._id !== id);
                } catch (e) {
                    console.log(e);
                }
            },
            switchStopped: function (task) {
                task.stopped = !task.stopped;
                this.updateTask(task);
            },
            getLogs: async function (id) {
                try {
                    let response = await axios.get(`http://localhost:3000/api/v1/task/log/${id}`);
                    this.logs = response.data.log
                } catch (e) {
                    this.$notify({
                        group: 'app',
                        title: 'Error obteniendo los logs',
                        text: e.toString(),
                    });
                }


            },
            getCrontabs: async function () {
                try {
                    let response = await axios.get(`http://localhost:3000/api/v1/task/import/crontab`);
                    this.cronImport = response.data.crons;
                } catch (e) {
                    console.log(e)
                }
            },
            importCrontab: async function (cronFile) {
                let response = await axios.post(`http://localhost:3000/api/v1/task/import/crontab/${this.cronUserImport}`, {
                    filename: cronFile
                });
                console.log(response);
            },
            getTaskFilter: function (id) {
                let filter = this.tasks.filter(el => el._id === id)
                return filter.length > 0 ? filter[0] : {}
            }
        },
        mounted() {
            this.getAllTasks();
            this.getAllUsers();
        },
        computed: {
            job: {
                get() {
                    return `${this.newTask.minute} ${this.newTask.hour} ${this.newTask.day} ${this.newTask.month} ${this.newTask.week} ${this.newTask.command}`
                },
                set(value) {
                    this.fullJob = value
                }
            },
        },
    }
</script>

<style scoped>
    #tasks .f-l {
        float: left;
    }

    #tasks .t-a-l {
        text-align: left;
    }

    #cronModal .modal-body {
        overflow-y: auto;
        max-height: 400px;
    }


</style>